.menu {
  display: flex;
  flex-direction: column;

  height: 30vh;
  background-color: $basic-bg-dark;
  color: $white;
  font-weight: bold;
  text-transform: uppercase;
  padding-block: 20px;

  transition: transform $effect-duration;
  transform: translateX(100%);
  overflow: auto;

  &__top {
    position: sticky;
    top: 0;
    z-index: 1;
    background-color: $basic-bg-dark;
  }

  @include on-desktop-mini {
    display: none;

    &:target {
      transform: translateX(0);
    }
  }
}
